.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
    padding-top: 50px;
    @include md {
        padding-top: 36px; }
    @include sm {
        padding-top: 8px; }
    &__container {
        display: flex;
        align-items: flex-end;
        @include sm {
            align-items: center; } }
    &__logo {
        margin-right: auto; } }

.nav {
    position: relative;
    @include sm {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        padding-top: 63px;
        text-transform: uppercase;
        background-color: rgba(#000, 0.96);
        transition: opacity 0.7s, visibility 0.7s;
        z-index: -1; }
    &.is-open {
        @include sm {
            visibility: visible;
            opacity: 1;
            transition: opacity 0.7s, visibility 0.7s; } }
    &__list {
        display: flex;
        justify-content: space-between;
        // align-items: center
        align-items: flex-start;
        @include sm {
            flex-flow: column;
            padding: 16px 0; } }
    &__item {
        color: #fff;
        margin-left: 50px;
        &:first-child {
            margin-left: 0; }
        @include sm {
            margin: 16px auto;
            &:first-child {
                margin: 16px auto; } } }
    &__link {
        font-size: 16px;
        font-weight: 300; }
    &__phone {
        font-size: 24px;
        display: block;
        line-height: 1; }
    &__email {
        display: block;
        margin-top: 4px;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 300; } }





// ===== LOGO =====

.logo {
    display: block;
    &__text {
        display: inline-block;
        color: #fff;
        font-size: 67px;
        font-family: $corinthia;
        font-weight: 400;
        line-height: 1;
        vertical-align: middle;
        @include md {
            font-size: 56px; }
        @include sm {
            font-size: 40px; } }
    &__img {
        display: inline-block;
        vertical-align: middle;
        margin-right: 30px;
        @include sm {
            margin-right: 12px;
            img {
                height: 48px; } } } }

.logo--vert {
    text-align: center;
    .logo__text {
        display: block;
        color: #333333; }
    .logo__img {
        display: block;
        margin-right: 0;
        margin-bottom: 24px; } }

// ===== TOGGLE BUTTON =====
.toggle-button {
    position: relative;
    z-index: 20;
    width: 32px;
    height: 24px;
    display: none;
    @include sm {
        display: block; }
    span {
        display: block;
        width: 100%;
        height: 2px;
        width: 24px;
        background: white;
        margin: 4px auto;
        transition: all 0.3s;
        backface-visibility: hidden; } }

.toggle-button.on {
    span:nth-child(1) {
        transform: rotate(45deg) translate(2px, 2px); }
    span:nth-child(2) {
        opacity: 0; }
    span:nth-child(3) {
        transform: rotate(-45deg) translate(6px, -7px); } }


