.catering {
    margin-bottom: 95px;
    @include sm {
        margin-bottom: 36px; }
    &__descr {
        margin-bottom: 58px;
        font-size: 16px;
        font-weight: 300;
        line-height: 2;
        text-align: center;
        color: #090909;
        font-style: italic;
        padding: 0 18px; }

    &__options {
        @include clr; }

    &__column {
        float: left;
        width: calc( 50% - 15px );
        display: flex;
        flex-flow: column;
        justify-content: space-around;
        height: 100%;
        @include sm {
            float: none;
            width: 100%; } }


    &__column:first-child {
        margin-right: 30px;
        overflow: hidden;
        @include sm {
            margin-right: 0; } }

    &__item {
        margin-bottom: 30px;
        position: relative;
        // display: table
        overflow: hidden;
        // overflow-y: hidden
        // overflow-x: hidden
        // table-layout: fixed
        &:hover {
            .catering__image {
                transform: scale(1.1);
                transition: transform 1s ease-out; }
            .catering__text-hidden {
                opacity: 1;
                visibility: visible;
                transition: opacity 0.5s ease-out, visibility 0.5s ease-out;
                transition-delay: 0.2s; }
            .catering__text-visible {
                opacity: 0;
                visibility: hidden;
                transition: opacity 0.5s ease-out, visibility 0.5s ease-out; } } }
    &__item-wrapper {
        display: table;
        width: 100%;
        height: 100%; }
    &__image {
        @include cover;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        transition: transform 1s ease-out;
        overflow: auto; }


    &__text {
        &-visible {
            position: absolute;
            background-color: #fff;
            padding: 8px 20px;
            font-size: 20px;
            font-weight: 300;
            line-height: 1;
            color: #333;
            transition: opacity 0.5s ease-out, visibility 0.5s ease-out;
            transition-delay: 0.2s;
            &--left-bottom {
                left: 0;
                bottom: 40px; }
            &--left-top {
                left: 0;
                bottom: 40px; }
            &--right-bottom {
                right: 0;
                bottom: 40px; }
            &--right-top {
                right: 0;
                top: 40px; }
            @include md {
                font-size: 16px;
                padding: 6px 12px; } }

        &-hidden {
            text-align: center;
            vertical-align: middle;
            display: table-cell;
            opacity: 0;
            visibility: hidden;
            font-size: 20px;
            line-height: 2.1;
            text-align: center;
            color: #000000;
            background-color: rgba(255,255,255,.7);
            width: 100%;
            padding: 30px 60px 30px;
            transition: opacity 0.7s ease-out, visibility 0.7s ease-out;
            @include md {
                font-size: 16px;
                padding: 20px; } } }





    // .image
    //     &-big
    //         padding-bottom: 70%
    //         // min-height: 70%
    //     &-middle
    //         padding-bottom: 60%
    //         // min-height: 60%
    //     &-small
    //         padding-bottom: 40%
    //         // min-height: 40%

    .image {
        &-big {
            height: 375px; }
        &-middle {
            height: 325px; }
        &-small {
            height: 225px; }
        @include md {
            &-big {
                height: 250px; }
            &-middle {
                height: 250px; }
            &-small {
                height: 250px; } }
        @include sm {
            // &-big
            //     min-height: 150px
            // &-middle
            //     min-height: 150px
            // &-small
            //     min-height: 150px
            &-big {
                height: 40vh; }
            &-middle {
                height: 40vh; }
            &-small {
                height: 40vh; } } } }


